const defaultState = {
    index: 1,
    menuOpen: false,
    total: 2,
    animating:false,
}

const navBarReducer = (state = defaultState, action) => {
    switch (action.type) {

        case 'updateIndex':
            return { ...state, index: (state.index + action.payload + state.total) % state.total };

        case 'setIndex':
            return { ...state, index: action.payload }

        case 'setMenuOpen':
            return { ...state, menuOpen: action.payload }

        case 'setAnimating':
                return { ...state, animating: action.payload }

        default:
            return state;
    }
}

export default navBarReducer;