import React from 'react';
import { ReactComponent as Github } from '../assets/icons/Github.svg';
import { ReactComponent as Gmail } from '../assets/icons/Gmail.svg';
import { ReactComponent as Linkedin } from '../assets/icons/Linkedin.svg';
import { useSelector } from 'react-redux';

const GmailIconGroup = ({ className }) => {
    const iconColors = ['white', 'black'];
    const { index } = useSelector(state => state.navBarReducer);

    const icon = {
        display: 'flex',
        flex: 1,
        cursor: 'pointer',
        marginTop: '10vh',
        marginLeft: '10px',
        padding: '10px',
        width: '1.7em',
        height: '1.7em',
        fill: className && className.indexOf('Menu') !== -1 ? 'white' : iconColors[index],
        stroke: className && className.indexOf('Menu') !== -1 ? 'white' : iconColors[index]

    }
    return (
        <div className={className}>
            <Github style={icon} id='iconGit' onClick={() => {
                let url = 'https://github.com/rohanakar';
                window.open(url, 'sharer1');
            }} />
            <Gmail style={icon} id='iconGit' onClick={() => {
                let url = 'https://mail.google.com/mail/?view=cm&fs=1&tf=1&to=rishabhc329@gmail.com&su=Portfolio Contact&body=Hi There,\n&ui=2&tf=1&pli=1';
                window.open(url, 'sharer', 'toolbar=0,status=0,width=648,height=395');
            }} />
            <Linkedin style={icon} id='iconGit' onClick={() => {
                let url = 'https://www.linkedin.com/in/rishabh-chaudhary/';
                window.open(url, 'sharer2');
            }} />
        </div>
    )
}

export { GmailIconGroup };