import React, { useState, useRef, useEffect } from 'react';
import Background from '../assets/background.png';
import { useDispatch } from 'react-redux';
import {incrementIndex} from '../redux/actions';
import {onTouchEnd, onTouchMove,onTouchStart} from '../utils/touchHandler';

// onTouchMove={touchMoveEvent => this.handleTouchMove(touchMoveEvent)}
// onTouchEnd={() => this.handleTouchEnd()}

const Landing = () => {

  const dispatch = useDispatch();
  const [loaded, setLoaded] = useState(false);
  const image = useRef(null);
  const refreshIntervalId = useRef(null);

  useEffect(() => {

    if (image.current && !refreshIntervalId.current)
      refreshIntervalId.current = setInterval(() => {
        if (image.current) {
          setLoaded(image.current.complete)
        }
      }, 100);
  })
  if (loaded) {
    clearInterval(refreshIntervalId.current);
  }

  return (
    <section id='Landing' 
    onTouchStart={touchStart=>onTouchStart(touchStart)}
    onTouchMove={touchMoveEvent => onTouchMove(touchMoveEvent,dispatch)}
    onTouchEnd = {()=>{onTouchEnd()}}
    >
      <div className='background'>
        <img alt='' ref={image} src={Background} />
      </div>
      <div className='backgroundHolder' style={!loaded ? {
      } : { opacity: '0',zIndex:11}}>
      </div>

      <div className='content'>
        <div className='heading'>
        Bringing Life to Ideas
          </div>
        <div id='button' onClick={() => dispatch(incrementIndex())}>
          Projects
        </div>
      </div>
      <div className='footer'>
        use arrow keys to navigate
      </div>
    </section>

  );
}

export default Landing;