import React, { useState, useEffect } from 'react';
import 'react-awesome-slider/dist/styles.css';
import 'react-awesome-slider/dist/custom-animations/cube-animation.css';
import AwesomeSlider from 'react-awesome-slider';
import Landing from './screens/Landing';
import Projects from './components/Projects';

import Sidebar from './components/Sidebar';
import {GmailIconGroup} from './components/Navigate';
import { useSelector, useDispatch } from 'react-redux';
import { incrementIndex, decrementIndex, setIndex,notAnimating,closeMenu } from './redux/actions';

const App = () => {
  const { index } = useSelector(state => state.navBarReducer);
  const dispatch = useDispatch();
  const [transition, setTransition] = useState(true);

  const Slider = (

    <AwesomeSlider
      animation="cubeAnimation"
      fillParent={true}
      onFirstMount={() => { setTransition(false) }}
      bullets={false}
      buttons={false}
      selected={index}
      mobileTouch={false}
      onTransitionStart={() => setTransition(true)}
      onTransitionEnd={(e) => {
        dispatch(setIndex(e.currentIndex));
        dispatch(notAnimating());
        dispatch(closeMenu());
        setTimeout(() => {
          setTransition(false);
        }, 400);
      }}>
      <div id='0'><Landing /></div>
      <div id='1'><Projects /></div>
    </AwesomeSlider>
  );

  useEffect(() => {

    const keyHandler = (event) => {

      if (transition)
        return;
      switch (event.key) {
        case "ArrowLeft":
          event.preventDefault();
          console.info("S");
          dispatch(incrementIndex());
          break;

        case "ArrowRight":
          event.preventDefault();
          console.info("S");
          dispatch(decrementIndex());
          break;

        case "ArrowUp":
          event.preventDefault();
          document.getElementById(Slider.props.selected).scrollTop -= 800;
          break;

        case "ArrowDown":
          // Down pressed 
          event.preventDefault();
          document.getElementById(Slider.props.selected).scrollTop += 800;
          break;

        default:
          break;
      }
    }
    window.addEventListener("keydown", keyHandler);
    return () => {
      window.removeEventListener("keydown", keyHandler);
    };
  });


  return (
    <div>
      <Sidebar />
      {Slider}
      <GmailIconGroup className='gmailIcons'/>
    </div>
  );
}

export default App;
