import React from 'react';
// import Project from './Project';
import { /*useSelector,*/ useDispatch } from 'react-redux';
import { onTouchMove, onTouchStart, onTouchEnd } from '../utils/touchHandler';



const Landing = () => {
    const dispatch = useDispatch();
    // const { projectsContent } = useSelector(state => state.projectReducer);
    return (
        <section id='Project'
            onTouchStart={touchStart => onTouchStart(touchStart)}
            onTouchMove={touchMoveEvent => onTouchMove(touchMoveEvent, dispatch)}
            onTouchEnd={() => { onTouchEnd() }}>
            <div className='content'>
                <div className='heading'>
                    Self Projects
                </div>
                <div className='subheading'>
                    These are various side projects that I worked upon my free time to explore and
                    improvise various tools and technologies. You can filter by various tags .
                </div>
                {/* <div className='projects'>
                    {getJsx(projectsContent)}
                </div> */}
            </div>
        </section>
    );
}

