// ----------- menu -----------------
export const closeMenu = () =>{
    return {
        type : 'setMenuOpen',
        payload : false,
    }
}

export const openMenu = () =>{
    return {
        type : 'setMenuOpen',
        payload : true,
    }
}
// ----------- menu -----------------


// ----------- index -----------------
export const incrementIndex = () =>{
    return {
        type : 'updateIndex',
        payload : 1,
    }
}
export const decrementIndex = () =>{
    return {
        type : 'updateIndex',
        payload : 1,
    }
}
export const setIndex = (val) =>{
    return {
        type : 'setIndex',
        payload : val,
    }
}

// ----------- index -----------------

// ----------- animating -----------------

export const Animating = () =>{
    return {
        type : 'setAnimating',
        payload : true,
    }
}

export const notAnimating = () =>{
    return {
        type : 'setAnimating',
        payload : false,
    }
}

