import {incrementIndex,decrementIndex} from '../redux/actions';

let clientX=[];
let clientY=[];
let id = -1;

export const onTouchStart = (touchStart) =>{
    id = touchStart.changedTouches[0].identifier;
    clientX=[];
    clientY=[];
}

export const onTouchMove = (touchMoveEvent) =>{
    // if(touchMoveEvent.changedTouches[0].identifiedTouch(id)<=50 || touchMoveEvent.touches[0].clientX>=window.screen.width-50){
    //     if(touchMoveEvent.touches[0].clientX <=50)
    //       dispatch(decrementIndex());
    //     else
    //       dispatch(incrementIndex());

    //   }
    clientX.push(touchMoveEvent.changedTouches[id].clientX);
    clientY.push(touchMoveEvent.changedTouches[id].clientY);
}

export const onTouchEnd = (dispatch) =>{
  clientY.sort();
  if(clientY[clientY.length-1]-clientY[0] <=100){
    if(clientX[0]>clientX[clientX.length-1])
      dispatch(decrementIndex());
    else
      dispatch(incrementIndex());
  }
  
}

